import "../styles/about.css";
import Fade from "react-reveal/Fade";
function AboutUs() {
  return (
    <div className="about-container">
      <Fade left>
        <h1 className="about-header">ABOUT CATHERINE RECRUITING</h1>
      </Fade>
      <Fade right cascade>
        <div className="about-section">
          <h1>Who we are</h1>
          <h3>
            Catherine Recruiting est firma repertricis eximia, qua diligentia et
            peritia in eligendis optimis candidatis praecellit. Nomen eius
            Catherine non solum notum est in mundo rei gestae, sed etiam
            insigniter probatum in creando connexiones idoneas inter
            opportunitates et talenta. Per Artem suam, Catherine Recruiting in
            omnibus negotiis, a magnis usque ad parva, aedificat pontes ad
            successum et progressum. Haec societas non solum talenta coniungit,
            sed etiam futurum brillantem creare intendit.
          </h3>
          <h2>We're different than the rest</h2>
          <h3>
            Catherine Recruiting excellit inter caeteras societates repertionis
            ex singularibus proprietatibus quae nos ab aliis differant. Primum,
            inquisitio nostra penetrat ultra curriculum vitae, scrutans non
            solum peritiam sed etiam virtutes et aptitudines personarum.
            Secundo, methodus nostra innovationis et constantis evolutio
            demonstrant nos non solum aptos esse ad mutata negotia, sed etiam
            anteire eas. Tertio, connexiones longae et bene structae nobis
            praesunt, quae non solum ad candidatos idoneos ducunt, sed etiam ad
            creandam communitatem valde cohaerentem. Quarto, nostra ratio
            transparentiae et honestitatis in omnibus transactionibus nobis
            commendat, quod rares est in industria. Quinto, dedicatio nostra
            erga clientis successum magis est quam simplicis emolumenti
            quaestus. Sexto, processus noster efficiens et personalis, cum
            consilio experto, distinguit nos in efficacia et eflicientia.
            Septimo, aedificamus partnership durable, et non simpliciter
            relationes transactionales. Octavo, Catherine Recruiting non modo
            talenta reperit, sed etiam sartorias opportunitates exigit, quod nos
            facit societate exceptionale in mundo repertionis.
          </h3>
          <div className="about-buttons">
            <h1>Find a job</h1>
            <h1>Work with us</h1>
          </div>
        </div>
      </Fade>
    </div>
  );
}

export default AboutUs;
